body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
} 

.hr { 
  margin-top: 15px;
}

button {  
  overflow: hidden; 
  text-decoration: none;
  font-size: 13px;
  border: 0;
  text-transform: uppercase; 
  padding: 0.4em 1em;
  border-radius: 3px;
  transition: 0.3s;
  box-shadow: 0 2px 4px #00000080;  
  text-align: center;
  background-color: #86959e;
  color: white;
}

button:hover {
  box-shadow: 1px 3px 6px #00000080;
}

input  { 
  border-color: #929292;
  border-width: 0px 0px 2px 0px;
}

input:hover {
  box-shadow: 0px 1px 0px #00000080;
}
.Header { 
    width: 100%; 
    height: 75px;

    text-align: left;  

    margin: 0;

    background-color: #ccffff;
    
    box-shadow: 0 2px 3px #00000080;  
}

h1 { 
    margin: 0;
    padding-left: 1em;
    font-weight: 300;
}
tr { 
    display: block; 
    float: left;   
    white-space: nowrap;
}
 
th, td { 
    display: block;   
    white-space: nowrap;
    text-align: center;
}

td input {   
    font-family: monospace;
}

td input:hover { 
    padding: 0px;
    outline: #333 solid 1px;
}
 
.tuner_input { 
    width: 5%;
}
.container { 
  margin-left: 20px;
  width: 95%;
}

button, select, input { 
  margin-left: 5px;
}

tr.tr { 
  display: table-row;
  float: none;
  width: auto;
}

td.option { 
  display: table-cell; 
  text-align: right;
}

td input:hover { 
  box-shadow: 0px 1px 0px #00000080; 
  outline: 0px;  
}

.textarea { 
  width: 400px;
  height: 150px;
}
